import React, { Component } from 'react'
import './Contact.css'
import axios from 'axios'
import swal from 'sweetalert'


class Contact extends Component {

  constructor() {
    super()

    this.state = {
      name: '',
      email: '',
      message: ''
    }

    this.handleChange = this.handleChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
  }

  handleChange(event) {
    this.setState({
      [event.target.name] : event.target.value
    })
  }

  handleSubmit(event) {
    event.preventDefault()
    const { name, email, message } = this.state

    this.setState({
      name: '',
      email: '',
      message: ''
    })

    axios.post('https://portfoliomailer.herokuapp.com/api/form', {
      name,
      email,
      message
    }).then(function(res) {
      swal({
        title: name,
        text: "Thank you for your message! I will get back to you soon.",
        icon: "success",
        button: "Aww yiss!",
      })
    }).catch(function (error) {
      swal({
        title: 'Oops! Something went wrong.',
        text: 'Shoot me an email at ntinvo@gmail.com',
        icon: "error",
        button: "OK",
      })
    })
  }

  render() {
    return (
      <div id='contact' className='section-area text-center' >
        <h2>CONTACT</h2><br/>
        <div className='lead lead-contact'><i>Question? Send me a message.</i></div>
        <div className='container text-center contactformdiv'>
          <form className='contactform justify-content-center' onSubmit={this.handleSubmit}>
            <div className='form-group'>
              <input type='name' className='form-control contactforminput' name='name' id='name' required
              placeholder='Enter your name' value={this.state.name} onChange={this.handleChange}/>
            </div>
            <div className='form-group'>
              <input type='email' className='form-control contactforminput' name='email' id='email' required
              placeholder='Enter your email' value={this.state.email} onChange={this.handleChange}/>
            </div>
            <div className='form-group'>
              <textarea className='form-control contactforminput textarea' name='message' id='message' row='3' required
                        placeholder='Your message' value={this.state.message} onChange={this.handleChange}>
              </textarea>
            </div>
            <button type='submit' className='btn btn-secondary send-button'>SEND</button>
          </form>
        </div>
        <br />
      </div>
    )
  }
}

export default Contact



